type TDiscount = {
  cover: string;
  storeName: string;
  discountText: string;
};

type TProps = {
  discounts: TDiscount[];
};

export function Discount({ data }: { data: TDiscount }) {
  return (
    <div className="container">
      <img src={data.cover} />
      <div>
        <h2 className="title">{data.storeName}</h2>
        <p className="description">{data.discountText}</p>
      </div>
      <style jsx>{`
        .container {
          display: flex;
          align-items: center;
          color: #7F7F7F;
          padding: 12px 0;
          border-bottom: solid .5px;
        }

        .title {
          color: rgba(0, 0, 0, 0.85);
          font-weight: 500;
          font-size: 16px;
        }

        .description {
          color: rgba(0, 0, 0, 0.45);
          font-size: 14px; 
        }

        .container:last-child {
          border-bottom: none;
        }
      `}</style>
    </div>
  );
}

export default function Discounts({ discounts }: TProps) {
  return (
    <div className="container">
      {discounts.map((discount, idx) => (
        <Discount key={`${idx}: ${discount.storeName}`} data={discount} />
      ))}
      <style jsx>{`
        .container {
          margin: 16px 0;
          padding: 16px;
          border-radius: 10px;
          background-color: white;
        }
      `}</style>
    </div>
  );
}
